.progress-text{
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 1;
    display: flex;
}

.progress-text-y-top{
    align-items: flex-start;
}

.progress-text-y-center{
    align-items: center;
}

.progress-text-y-bottom{
    align-items: flex-end;
}

.progress-text-x-left{
    justify-content: flex-start;
}

.progress-text-x-center{
    justify-content: center;
}

.progress-text-x-right{
    justify-content: flex-end;
}