.composite.composite-slides{
    position: relative;
}
.composite-arrow{
    position: absolute;
    z-index: 1;
}
.composite-arrow-0{
    left: 0
}
.composite-arrow-1{
    right: 0
}
.composite-arrows-top > .composite-arrow{
    top: 0;
}
.composite-arrows-middle > .composite-arrow{
    top: 50%;
    transform: translateY(-50%);
}
.composite-arrows-bottom > .composite-arrow{
    bottom: 0;
}

.composite-arrows-top-middle > .composite-arrow-0{
    transform: translateX(-50%);
}
.composite-arrows-top-middle > .composite-arrow-1{
    transform: translateX(50%);
}
.composite-arrows-middle-middle > .composite-arrow-0{
    transform: translate(-50%, -50%);
}
.composite-arrows-middle-middle > .composite-arrow-1{
    transform: translate(50%, -50%);
}
.composite-arrows-bottom-middle > .composite-arrow-0{
    transform: translateX(-50%);
}
.composite-arrows-bottom-middle > .composite-arrow-1{
    transform: translateX(50%);
}

.composite-arrows-top-outer > .composite-arrow-0{
    transform: translateX(-100%);
}
.composite-arrows-top-outer > .composite-arrow-1{
    transform: translateX(100%);
}
.composite-arrows-middle-outer > .composite-arrow-0{
    transform: translate(-100%, -50%);
}
.composite-arrows-middle-outer > .composite-arrow-1{
    transform: translate(100%, -50%);
}
.composite-arrows-bottom-outer > .composite-arrow-0{
    transform: translateX(-100%);
}
.composite-arrows-bottom-outer > .composite-arrow-1{
    transform: translateX(100%);
}
