.ntx-multimedia-volume-control{
  padding: 0;
  border: 0;
  background: inherit;
  margin: 0;
  display: inline-block;
  vertical-align: middle;
  position: relative;
}

.ntx-multimedia-volume-control > .ntx-multimedia-btn-sound{
  padding: 0;
  border: 0;
  background: inherit;
  background-size: 24px 24px;
  background-position: center;
  background-repeat: no-repeat;
  cursor: pointer;
  outline: none;
  width: 1.5rem;
  height: 1.5rem;
  margin: 0 0.5rem 0 0.5rem;
  display: inline-block;
  vertical-align: middle;
}

.ntx-multimedia-volume-control > .ntx-multimedia-btn-sound.ntx-multimedia-btn-sound-mute{
  background-image: url(./volume_off.svg);
}
.ntx-multimedia-volume-control > .ntx-multimedia-btn-sound.ntx-multimedia-btn-sound-unmute{
  background-image: url(./volume.svg);
}

.ntx-multimedia-volume-control > .ntx-multimedia-wrapper-slider-sound{
  display: none;
  position: absolute;
  bottom: 1.5rem;
  left: 50%;
  width: 2rem;
  height: 7rem;
  transform: translateX(-50%);
}
.ntx-multimedia-volume-control > .ntx-multimedia-wrapper-slider-sound .ntx-multimedia-container-slider-sound{
  background-color: #fff;
  padding: 0.5rem 0;
  height: 7rem;
  margin: 0 0.375rem;
  box-shadow: 0px 0px 8px #00000029;
  border-radius: 2px;
}

.ntx-multimedia-volume-control > .ntx-multimedia-wrapper-slider-sound input[type="range"].ntx-multimedia-range-sound{
  -webkit-appearance: none;
  margin: 0;
  width: 6rem;
  height: 1.25rem;
  transform-origin: 3rem 3rem;
  transform: rotate(-90deg);
  cursor: pointer;
}

/*** Firefox ***/
.ntx-multimedia-volume-control > .ntx-multimedia-wrapper-slider-sound input[type="range"].ntx-multimedia-range-sound::-moz-range-track {
  width: 100%;
  height: 0.25rem;
  background-color: #EEEEEE;
  border-radius: 4px;
}
.ntx-multimedia-volume-control > .ntx-multimedia-wrapper-slider-sound input[type="range"].ntx-multimedia-range-sound::-moz-range-progress {
  background-color: #333333;
  height: 0.25rem;
  border-radius: 4px;
}
.ntx-multimedia-volume-control > .ntx-multimedia-wrapper-slider-sound input[type="range"].ntx-multimedia-range-sound::-moz-range-thumb {
  visibility: hidden;
}

/*** Chrome ***/
.ntx-multimedia-volume-control > .ntx-multimedia-wrapper-slider-sound input[type="range"].ntx-multimedia-range-sound::-webkit-slider-runnable-track {
  width: 100%;
  height: 0.25rem;
  background-color: #EEEEEE;
  border-radius: 4px;
  border: 0 solid #EEEEEE;
}
.ntx-multimedia-volume-control > .ntx-multimedia-wrapper-slider-sound input[type="range"].ntx-multimedia-range-sound::before {
  position: absolute;
  content: "";
  top: 0.5rem;
  left: 0;
  width: var(--volume-percent);
  height: 0.25rem;
  background-color: #333333;
  border-radius: 4px;
}
.ntx-multimedia-volume-control > .ntx-multimedia-wrapper-slider-sound input[type="range"]::-webkit-slider-thumb {
  visibility: hidden;
}


.ntx-multimedia-volume-control:hover .ntx-multimedia-wrapper-slider-sound{
  display: inline-block;
}
