.ntx-multimedia-progress-bar{
  position: relative;
}

.ntx-multimedia-progress-bar > input[type="range"].ntx-multimedia-range-seek {
  position: relative;
  -webkit-appearance: none;
  width: 100%;
  margin: 0;
  padding: 0;
  height: 1.25rem;    
  outline: none;
  cursor: pointer;
  background-color: transparent;
  display: inline-block;
  vertical-align: middle;
  border-radius: 4px;
}

/*** Firefox ***/
.ntx-multimedia-progress-bar > input[type="range"].ntx-multimedia-range-seek::-moz-range-track {
  width: 100%;
  height: 0.25rem;
  background: linear-gradient(to right, #CCCCCC var(--buffered-width), #DDDDDD var(--buffered-width));
  border-radius: 4px;
}
.ntx-multimedia-progress-bar > input[type="range"].ntx-multimedia-range-seek::-moz-range-progress {
  background-color: #00B3FF;
  height: 0.25rem;
  border-radius: 4px;
}
.ntx-multimedia-progress-bar > input[type="range"].ntx-multimedia-range-seek::-moz-range-thumb {
  visibility: hidden;
}

/*** Chrome ***/
.ntx-multimedia-progress-bar > input[type="range"].ntx-multimedia-range-seek::-webkit-slider-runnable-track {
  width: 100%;
  height: 0.25rem;
  background: linear-gradient(to right, #CCCCCC var(--buffered-width), #DDDDDD var(--buffered-width));
  border-radius: 4px;
}
.ntx-multimedia-progress-bar > input[type="range"].ntx-multimedia-range-seek::before {
  position: absolute;
  content: "";
  top: 0.5rem;
  left: 0;
  width: var(--seek-before-width);
  height: 0.25rem;
  background-color: #00B3FF;
  border-radius: 4px;
}
.ntx-multimedia-progress-bar > input[type="range"].ntx-multimedia-range-seek::-webkit-slider-thumb {
  visibility: hidden;
}

.ntx-milestone-multimedia {
  position: absolute;
  transform: translateX(-50%);
  top: -2px;
  left: 0;
  border: 0;
  cursor: pointer;
  width: 1.25px;
  height: 7px;
}
.ntx-milestone-multimedia > span {
  position: absolute;
  transform: translateX(-50%);
  bottom: 0;
  left: 50%;
  width: 0; 
  height: 0; 
  border: 0;
  border-top: 0.4375rem solid orange;
  border-right: 0.3125rem solid transparent;  
  border-left: 0.3125rem solid transparent;
}