.ntx-audio-skin-1{
    position: relative;
    width: 100%;
    height: 4rem;
    --seek-before-width: 0%;
    --buffered-width: 0%;
    /* background-color: #E8E8E8; */
    font-size: 0;
    line-height: 0;
    overflow: hidden;
}
.ntx-audio-skin-1 > .ntx-audio-btn-play-pause{
    padding: 0;
    border: 0;
    background: inherit;
    background-size: 32px 32px;
    background-position: center;
    background-repeat: no-repeat;
    cursor: pointer;
    outline: none;
    width: 3rem;
    height: 3rem;
    margin: 0.5rem 0.75rem 0.5rem 0.5rem;
    display: inline-block;
    vertical-align: middle;
}
.ntx-audio-skin-1 > .ntx-audio-btn-play-pause.play{
    background-image: url(./icon-play.svg);
}
.ntx-audio-skin-1 > .ntx-audio-btn-play-pause.pause{
  background-image: url(./icon-pause.svg);
}

.ntx-audio-skin-1 > input[type="range"].ntx-audio-range-seek {
    position: relative;
    -webkit-appearance: none;
    width: calc(100% - 3rem - 0.5rem - 0.75rem - 7.5rem);
    padding: 0;
    height: 1.5rem;
    margin: 1.25rem 0 1.25rem 0;
    outline: none;
    background-color: transparent;
    display: inline-block;
    vertical-align: middle;
  }

  .ntx-audio-skin-1 > input[type="range"].ntx-audio-range-seek::-webkit-slider-runnable-track {
    width: 100%;
    height: 6px;
    cursor: pointer;
    background: linear-gradient(to right, #CCCCCC var(--buffered-width), #DDDDDD var(--buffered-width));
  }
  .ntx-audio-skin-1 > input[type="range"].ntx-audio-range-seek::before {
    position: absolute;
    content: "";
    top: 9px;
    left: 0;
    width: var(--seek-before-width);
    height: 6px;
    background-color: #00B3FF;
    cursor: pointer;
  }
  .ntx-audio-skin-1 > input[type="range"].ntx-audio-range-seek::-webkit-slider-thumb {
    position: relative;
    -webkit-appearance: none;
    box-sizing: content-box;
    border: 1px solid #00B3FF;
    height: 15px;
    width: 15px;
    border-radius: 50%;
    background-color: #fff;
    cursor: pointer;
    margin: -7px 0 0 0;
    visibility: hidden;
  }
  .ntx-audio-skin-1 > input[type="range"].ntx-audio-range-seek:active::-webkit-slider-thumb {
    transform: scale(1.2);
    background: #00B3FF;
  }



  .ntx-audio-skin-1 > input[type="range"].ntx-audio-range-seek::-moz-range-track {
    width: 100%;
    height: 6px;
    cursor: pointer;
    background: linear-gradient(to right, #CCCCCC var(--buffered-width), #DDDDDD var(--buffered-width));
  }
  .ntx-audio-skin-1 > input[type="range"].ntx-audio-range-seek::-moz-range-progress {
    background-color: #00B3FF;
    height: 6px;
    cursor: pointer;
  }
  .ntx-audio-skin-1 > input[type="range"].ntx-audio-range-seek::-moz-focus-outer {
    border: 0;
  }
  .ntx-audio-skin-1 > input[type="range"].ntx-audio-range-seek::-moz-range-thumb {
    box-sizing: content-box;
    border: 1px solid #00B3FF;
    height: 15px;
    width: 15px;
    border-radius: 50%;
    background-color: #fff;
    cursor: pointer;
    visibility: hidden;
  }
  .ntx-audio-skin-1 > input[type="range"].ntx-audio-range-seek:active::-moz-range-thumb {
    transform: scale(1.2);
    background: #00B3FF;
  }



  .ntx-audio-skin-1 > input[type="range"].ntx-audio-range-seek::-ms-track {
    width: 100%;
    height: 6px;
    cursor: pointer;
    background: transparent;
    border: solid transparent;
    color: transparent;
  }
  .ntx-audio-skin-1 > input[type="range"].ntx-audio-range-seek::-ms-fill-lower {
    height: 6px;
    background-color: #00B3FF;
  }
  .ntx-audio-skin-1 > input[type="range"].ntx-audio-range-seek::-ms-fill-upper {
    background: linear-gradient(to right, #CCCCCC var(--buffered-width), #DDDDDD var(--buffered-width));
  }
  .ntx-audio-skin-1 > input[type="range"].ntx-audio-range-seek::-ms-thumb {
    box-sizing: content-box;
    border: 1px solid #00B3FF;
    height: 15px;
    width: 15px;
    border-radius: 50%;
    background-color: #fff;
    cursor: pointer;
    visibility: hidden;
  }
  .ntx-audio-skin-1 > input[type="range"].ntx-audio-range-seek:active::-ms-thumb {
    transform: scale(1.2);
    background: #00B3FF;
  }
  .ntx-audio-skin-1 > .ntx-audio-time{
    display: inline-block;
    vertical-align: middle;
    width: 7.5rem;
    font-size: 14px;
    line-height: 17px;
    text-align: center;
    font-family: 'LatoWeb', sans-serif;
    font-weight: 400;
    color: #666;
  }