.pageContent{
    position: relative;
}
.pageContent-menu-button{
    position: fixed;
    z-index: 2;
    top: 0;
    left: 0;
    width: auto;
}
#EditorContentContainer .pageContent-menu-button{
    position: absolute;
}

.pageContent-arrow{
    position: fixed;
    z-index: 1;
}
#EditorContentContainer .pageContent-arrow{
    position: absolute;

}
.pageContent-arrow-0{
    left: 0
}
.pageContent-arrow-1{
    right: 0
}
.pageContent-arrows-top > .pageContent-arrow{
    top: 0;
}
.pageContent-arrows-middle > .pageContent-arrow{
    top: 50%;
    transform: translateY(-50%);
}
.pageContent-arrows-bottom > .pageContent-arrow{
    bottom: 0;
}

.pageContent-arrows-top-middle > .pageContent-arrow-0{
    transform: translateX(-50%);
}
.pageContent-arrows-top-middle > .pageContent-arrow-1{
    transform: translateX(50%);
}
.pageContent-arrows-middle-middle > .pageContent-arrow-0{
    transform: translate(-50%, -50%);
}
.pageContent-arrows-middle-middle > .pageContent-arrow-1{
    transform: translate(50%, -50%);
}
.pageContent-arrows-bottom-middle > .pageContent-arrow-0{
    transform: translateX(-50%);
}
.pageContent-arrows-bottom-middle > .pageContent-arrow-1{
    transform: translateX(50%);
}

.pageContent-arrows-top-outer > .pageContent-arrow-0{
    transform: translateX(-100%);
}
.pageContent-arrows-top-outer > .pageContent-arrow-1{
    transform: translateX(100%);
}
.pageContent-arrows-middle-outer > .pageContent-arrow-0{
    transform: translate(-100%, -50%);
}
.pageContent-arrows-middle-outer > .pageContent-arrow-1{
    transform: translate(100%, -50%);
}
.pageContent-arrows-bottom-outer > .pageContent-arrow-0{
    transform: translateX(-100%);
}
.pageContent-arrows-bottom-outer > .pageContent-arrow-1{
    transform: translateX(100%);
}
